.checkout-page {
  padding: 20px;
}

.checkout-container {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

/* ADDRESS BOX */
.address-box {
  flex: 2;
  background: white;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.address-box input,
.address-box textarea {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  outline: none;
  font-size: 15px;
}

.address-box textarea {
  height: 70px;
}

/* SUMMARY BOX */
.summary-box {
  flex: 1;
  background: #fafafa;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #ddd;
  height: fit-content;
}

.summary-item {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.summary-item img {
  width: 60px;
  height: 60px;
  border-radius: 6px;
  object-fit: cover;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin: 8px 0;
}

.summary-total {
  margin-top: 10px;
  font-size: 22px;
}

.place-btn {
  width: 100%;
  padding: 12px;
  background: #0d6efd;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
}

.place-btn:hover {
  background: #004ecb;
}

/* MOBILE */
@media (max-width: 768px) {
  .checkout-container {
    flex-direction: column;
  }

  .summary-box {
    width: 90%;
  }
}
